.create-event-container {
    max-width: 500px;
    margin: auto;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .create-event-container h1 {
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
  }
  
  .create-event-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .create-event-form label {
    font-size: 1rem;
    color: #333;
  }
  
  .create-event-form input, .create-event-form textarea {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .create-event-form button {
    padding: 10px;
    font-size: 1rem;
    background-color: #ff865b;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .create-event-form button:hover {
    background-color: #ff5733;
  }
  