/* App.css */

.events-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.event-card {
  border: 1px solid #ccc;
  padding: 20px;
  cursor: pointer;
  border-radius: 8px;
  transition: box-shadow 0.3s;
}

.event-card:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}